.Footer {

	@include font-heading;
	@include text-sm;
	color: $color-footer-text;
	border-top: 1px solid $border-color;
	margin: $margin-vertical-lg 0;

	.HMLogo-Tiny {
		margin: 0 $gutter-width 0 0;
		opacity: 0.6;
		position: relative;
		top: -1px;
	}

	a:link,
	a:visited {
		color: inherit;
	}

}
